<template>
  <div class="template-container">
    <div class="template-container-title">新建广告创意</div>
    <el-form ref="form" :model="form" :inline="true">
      <!--推广计划 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">推广计划</div>
        <div class="temp-item-main">
          <el-form-item label="所属推广计划">
            <el-select @change="campaignChange" v-model="form.campaign_id" filterable remote placeholder="选择推广计划" :remote-method="remoteCampaign" :loading="loadingCampaign">
              <el-option v-for="item in adCampaignsList" :key="item.campaign_id" :data-type="item.product_type" :label="item.campaign_name" :value="item.campaign_id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="newCampaign">新建计划</el-button>
          </el-form-item>
        </div>
      </div>
      <!--推广计划 end -->

      <!--站点 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">站点</div>
        <div class="temp-item-main">
          <label class="temp-item-label" for="">投放媒体</label>
          <div class="radio-group-inlineblock">
            <el-radio-group @change="siteSetListChange" v-model="form.site_set">
              <el-radio class="radio-group-item" v-for="(val,key) in site_setList" :key="key" :label="key">{{val}}</el-radio>
            </el-radio-group>
          </div>
        </div>
      </div>
      <!--站点 end -->

      <!--目标详情 begin -->
      <div class="temp-item-block" v-show='productShow'>
        <div class="temp-item-title">目标详情</div>
        <div class="temp-item-main">
          <el-form-item label="标的物">
            <el-select @change="productChange" v-model="form.product_refs_id" filterable placeholder="选择标的物">
              <el-option v-for="item in productList" :key="item.product_refs_id" :label="item.product_name" :value="item.product_refs_id"></el-option>
            </el-select>
          </el-form-item>
        </div>
      </div>
      <!--目标详情 end -->

      <!--落地页 begin -->
      <div class="temp-item-block" v-show='destinationUrlShow'>
        <div class="temp-item-title">落地页</div>
        <div class="temp-item-main">
          <el-form-item label="落地页url">
            <input class="temp-long-inputtext" v-model="form.destination_url" type="text" value="" maxlength="1023" />
          </el-form-item>
        </div>
      </div>
      <!--落地页 end -->

      <!--创意规格 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">创意规格</div>
        <div class="temp-item-main" style="display:inline-flex">
          <el-table ref="adCreativeTable" v-loading="loading" :data="adcreativeList" @current-change="handleCurrentChange" max-height="556" highlight-current-row style="width: 100%" :default-sort="{prop: 'date', order: 'descending'}">
            <el-table-column fixed prop="adcreative_template_id" label="ID" sortable>
            </el-table-column>
            <!-- <el-table-column :render-header="tableHeaderRender" prop="crt_style" label="创意形式" :formatter="formatterCrtstyle">
            </el-table-column> -->
            <el-table-column prop="adcreative_template_name" label="广告版位">
            </el-table-column>
            <el-table-column filter-placement="bottom-start" :filter-method="filterHandler" :filters="element_type_filter" prop="adcreative_template_style" label="创意形式" :formatter="formatterCrtstyle">
            </el-table-column>
            <el-table-column prop="adcreative_template_desc" label="创意规格描述">
            </el-table-column>
          </el-table>
          <div v-if="adcreativeTemplateInfo_row.adcreative_sample_image.image" style="float:right; width:200px;">
            <img style="width:100%" :src="adcreativeTemplateInfo_row.adcreative_sample_image.image" />
            <div class="sample_image_info">
              <p>{{ adcreativeTemplateInfo_row.adcreative_sample_image.name }} <br>
                <span>(广告可能出现在以上位置)</span>
              </p>
              <span>{{ adcreativeTemplateInfo_row.adcreative_template_size}}</span>
            </div>
          </div>
        </div>
      </div>
      <!--创意规格 end -->

      <!--上传创意 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">上传创意</div>
        <div class="temp-item-main">
          <el-tabs v-model="tabActiveName" @tab-click="handleTbaClick">
            <el-tab-pane v-for="(tabItem,tabIndex) in tabList" :key="tabItem.name" :label="tabItem.label" :name="tabItem.name">
              <div class="uploadurl-input">
                <el-input placeholder="请输入创意名称" name="adcreative_name" v-model="tabItem.adcreative_name" clearable></el-input>
              </div>

              <!-- 结构体 begin -->
              <div v-if="tabItem.elementsData.structList.length > 0 " class="uploadList-wrap" v-for="(uploadItem,index) in tabItem.elementsData.structList" :key="uploadItem.parent.name+index">

                <div v-for="(childrenList,i) in uploadItem.children">

                  <div v-for="(children,k) in childrenList">
                    <!-- 视频 -->
                    <div class="uploadList-li" v-if="children.element_type=='ELEMENT_TYPE_VIDEO'">
                      <input class="uploadfile-input" @change="uploadfileChange($event,index,tabIndex,'video',k,i)" type="file" />
                      <div class="uploadfile-item">

                        <div>
                          <i class="el-icon-upload icon-upload"></i>
                          <div class="el-upload__text">
                            将视频文件拖到此处，或
                            <em>点击上传</em>
                            <br>
                            <p class="upload-size">
                              {{ children.restriction.video_restriction.min_width || children.restriction.video_restriction.width }}px * {{ children.restriction.video_restriction.min_height || children.restriction.video_restriction.height }}px <br>
                              <span>（推荐尺寸）</span>
                            </p>
                          </div>
                        </div>
                        <div class="el-upload__tip" slot="tip">只能上传{{children.restriction.video_restriction.file_format_text}}文件，且不超过{{ children.restriction.video_restriction.file_size }}kb</div>
                      </div>
                      <el-progress :percentage="children.progressbar.percentage" :status="children.progressbar.status"></el-progress>
                    </div>
                    <!-- 图片 -->
                    <div class="uploadList-li" v-if="children.element_type=='ELEMENT_TYPE_IMAGE'">
                      <input class="uploadfile-input" @change="uploadfileChange($event,index,tabIndex,'image',k,i)" type="file" />
                      <div class="uploadfile-item">
                        <img class="uploadfile-preview" :src="children.preview_url" />
                        <div v-if="!children.preview_url">
                          <i class="el-icon-upload icon-upload"></i>
                          <div class="el-upload__text">
                            将图片文件拖到此处，或
                            <em>点击上传</em>
                            <br>
                            <p class="upload-size">
                              {{ children.restriction.image_restriction.width }}px * {{ children.restriction.image_restriction.height }}px <br>
                              <span>（推荐尺寸）</span>
                            </p>
                          </div>
                        </div>
                        <div class="el-upload__tip" slot="tip">只能上传{{children.restriction.image_restriction.file_format_text}}文件，且不超过{{ children.restriction.image_restriction.file_size }}kb</div>
                      </div>
                      <el-progress :percentage="children.progressbar.percentage" :status="children.progressbar.status"></el-progress>
                    </div>
                    <!-- 动画效果 -->
                    <div v-if="children.element_type=='ELEMENT_TYPE_ENUM'" class="uploadurl-input">
                      <el-select v-model="children.itemvalue" :placeholder="children.description">
                        <el-option v-for="(enumItem,index) in children.enum_property.enumeration" :key="enumItem.value" :value="enumItem.value" :label="enumItem.description"></el-option>
                      </el-select>
                    </div>
                    <!-- 文本 begin -->
                    <div v-if="children.element_type=='ELEMENT_TYPE_TEXT' && children.restriction.text_restriction" class="uploadurl-input">
                      <el-input :minlength="children.restriction.text_restriction.min_length" :maxlength="children.restriction.text_restriction.max_length" :placeholder="children.description" :name="children.name" v-model="children.itemvalue" clearable></el-input>
                    </div>
                    <div v-else-if="children.element_type=='ELEMENT_TYPE_TEXT' && !children.restriction.text_restriction" class="uploadurl-input">
                      <el-input :placeholder="children.description" :name="children.name" v-model="children.itemvalue" clearable></el-input>
                    </div>
                    <!-- URL begin -->
                    <div v-if="children.element_type=='ELEMENT_TYPE_URL'" class="uploadurl-input">
                      <el-input :placeholder="children.description" :name="children.name" v-model="children.itemvalue" clearable></el-input>
                    </div>
                  </div>

                </div>

              </div>
              <!-- 结构体 begin -->

              <!--  视频 begin -->
              <div v-if="tabItem.elementsData.videoList.length > 0 " class="uploadList-wrap">
                <!-- <el-upload class="upload-demo" v-for="(uploadItem, index) in tabItem.elementsData.videoList" :name="uploadItem.name" :key="uploadItem.name+index" :before-upload="beforeUpload" :data="upadlodData" :headers="uploadHeaders" :on-preview="uploadFile" drag action="/Admin/Marketing_Images/add">
                  <img style="display:none" v-bind:src="imgurl" />
                  <i class="el-icon-upload"></i>
                  <div class="el-upload__text">
                    将文件拖到此处，或
                    <em>点击上传</em>
                    <br>
                    <p class="upload-size">
                      {{ uploadItem.restriction.video_restriction.min_width }}px * {{ uploadItem.restriction.video_restriction.min_height }}px <br>
                      <span>（推荐尺寸）</span>
                    </p>
                  </div>
                  <div class="el-upload__tip" slot="tip">只能上传{{uploadItem.restriction.video_restriction.file_format_text}}文件，且不超过{{ uploadItem.restriction.video_restriction.file_size }}kb</div>
                </el-upload> -->
                <div class="uploadList-li" v-for="(uploadItem, index ) in tabItem.elementsData.videoList" :key="uploadItem.name+index">
                  <input class="uploadfile-input" @change="uploadfileChange($event,index,tabIndex,'video')" type="file" />
                  <div class="uploadfile-item">
                    <!-- <img class="uploadfile-preview" :src="uploadItem.preview_url" /> -->
                    <div>
                      <i class="el-icon-upload icon-upload"></i>
                      <div class="el-upload__text">
                        将视频文件拖到此处，或
                        <em>点击上传</em>
                        <br>
                        <p class="upload-size">
                          {{ uploadItem.restriction.video_restriction.min_width || uploadItem.restriction.video_restriction.width }}px * {{ uploadItem.restriction.video_restriction.min_height || uploadItem.restriction.video_restriction.height }}px <br>
                          <span>（推荐尺寸）</span>
                        </p>
                      </div>
                    </div>
                    <div class="el-upload__tip" slot="tip">只能上传{{uploadItem.restriction.video_restriction.file_format_text}}文件，且不超过{{ uploadItem.restriction.video_restriction.file_size }}kb</div>
                  </div>
                  <el-progress :percentage="uploadItem.progressbar.percentage" :status="uploadItem.progressbar.status"></el-progress>
                </div>
              </div>
              <!--  视频 end  -->
              <!--  图片 begin -->
              <div v-if="tabItem.elementsData.imageList.length > 0 " class="uploadList-wrap">
                <!-- <div class="upload-li" v-for="(uploadItem, index) in tabItem.elementsData.imageList" :key="uploadItem.name+index" @click="uploadFileLiclick(index,'image')">
                  <el-upload class="upload-demo" :limit="uploadlimit" :name="uploadItem.name" :on-change="uploadImgchange" :before-upload="beforeUpload" :data="upadlodData" :headers="uploadHeaders" :on-preview="uploadFile" drag action="/Admin/Marketing_Images/add">
                    <img style="display:none" v-bind:src="imgurl" />
                    <i class="el-icon-upload"></i>
                    <div class="el-upload__text">
                      将文件拖到此处，或
                      <em>点击上传</em>
                      <br>
                      <p class="upload-size">
                        {{ uploadItem.restriction.image_restriction.width }}px * {{ uploadItem.restriction.image_restriction.height }}px <br>
                        <span>（推荐尺寸）</span>
                      </p>
                    </div>
                    <div class="el-upload__tip" slot="tip">只能上传{{uploadItem.restriction.image_restriction.file_format_text}}文件，且不超过{{ uploadItem.restriction.image_restriction.file_size }}kb</div>
                  </el-upload>
                </div> -->
                <div class="uploadList-li" v-for="(uploadItem, index ) in tabItem.elementsData.imageList" :key="uploadItem.name+index">
                  <input class="uploadfile-input" @change="uploadfileChange($event,index,tabIndex,'image')" type="file" />
                  <div class="uploadfile-item">
                    <img class="uploadfile-preview" :src="uploadItem.preview_url" />
                    <div v-if="!uploadItem.preview_url">
                      <i class="el-icon-upload icon-upload"></i>
                      <div class="el-upload__text">
                        将图片文件拖到此处，或
                        <em>点击上传</em>
                        <br>
                        <p class="upload-size">
                          {{ uploadItem.restriction.image_restriction.width }}px * {{ uploadItem.restriction.image_restriction.height }}px <br>
                          <span>（推荐尺寸）</span>
                        </p>
                      </div>
                    </div>
                    <div class="el-upload__tip" slot="tip">只能上传{{uploadItem.restriction.image_restriction.file_format_text}}文件，且不超过{{ uploadItem.restriction.image_restriction.file_size }}kb</div>
                  </div>
                  <el-progress :percentage="uploadItem.progressbar.percentage" :status="uploadItem.progressbar.status"></el-progress>
                </div>
              </div>
              <!--  图片 end  -->
              <!-- 动画效果 begin -->
              <div v-if="tabItem.elementsData.enumList.length > 0 " v-for="(uploadItem,index) in tabItem.elementsData.enumList" :key="uploadItem.name+index" class="uploadurl-input">
                <el-select v-model="uploadItem.itemvalue" :placeholder="uploadItem.description">
                  <el-option v-for="(enumItem,index) in uploadItem.enum_property.enumeration" :key="enumItem.value" :value="enumItem.value" :label="enumItem.description"></el-option>
                </el-select>
              </div>
              <!-- 动画效果 begin -->
              <!-- 文本 begin -->
              <div v-if="tabItem.elementsData.textList.length > 0 " class="uploadurl-input" v-for="(uploadItem,index) in tabItem.elementsData.textList" :key="uploadItem.name+index">
                <div v-if="uploadItem.element_type=='ELEMENT_TYPE_TEXT' && uploadItem.restriction.text_restriction">
                  <el-input :minlength="uploadItem.restriction.text_restriction.min_length" :maxlength="uploadItem.restriction.text_restriction.max_length" :placeholder="uploadItem.description" :name="uploadItem.name" v-model="uploadItem.itemvalue" clearable></el-input>
                </div>
                <div v-else-if="uploadItem.element_type=='ELEMENT_TYPE_TEXT' && !uploadItem.restriction.text_restriction">
                  <el-input :placeholder="uploadItem.description" :name="uploadItem.name" v-model="uploadItem.itemvalue" clearable></el-input>
                </div>
              </div>
              <!-- 文本 end -->
              <!-- URL begin -->
              <div v-if="tabItem.elementsData.urlList.length > 0 " class="uploadurl-input" v-for="(uploadItem,index) in tabItem.elementsData.urlList" :key="uploadItem.name+index">
                <el-input :placeholder="uploadItem.description" :name="uploadItem.name" v-model="uploadItem.itemvalue" clearable></el-input>
              </div>
              <!-- URL end -->

            </el-tab-pane>
          </el-tabs>

        </div>
      </div>
      <!--上传创意 end -->

      <el-form-item>
        <el-button type="primary" :loading="submitLoading" @click="onSubmit">立即创建</el-button>
        <el-button @click="onCancel">取消</el-button>
      </el-form-item>
    </el-form>

  </div>

</template>



<script>
export default {
  created: function() {
    // 获取session
    var arr,
      reg = new RegExp('(^| )PHPSESSID=([^;]*)(;|$)')
    if ((arr = document.cookie.match(reg))) {
      this.uploadHeaders = {
        'VUE-SESSID': unescape(arr[2])
      }
    }
    // 获取页面id
    var params = this.$route.params || {}
    if (params.hasOwnProperty('id')) {
      // this.adgroupId = params.id
    }
    if (this.account_id) {
      this.getAdCampaigns(this.adCampaignsParams)
    }
    this.getAdCommongetEnum()
  },
  watch: {
    // 监听 account_id 发生变化
    account_id() {
      this.getAdCampaigns(this.adCampaignsParams)
    }
  },
  computed: {
    element_type_filter() {
      var list = []
      var map = {}
      this.adcreativeList.forEach(element => {
        if (!map.hasOwnProperty(element['adcreative_template_style'])) {
          map[element['adcreative_template_style']] =
            element['adcreative_template_style']
          list.push({
            text: element['adcreative_template_style'],
            value: element['adcreative_template_style']
          })
        }
      })

      return list
    },
    account_id() {
      var account = this.$store.getters.get_account_action
      return account.account_id
    }
  },
  data() {
    return {
      element_type_list: {}, // 创意规格的 类型
      submitLoading: false, // 禁止重复提交 -- loading 状态
      loadingCampaign: false,
      loading: false,
      productList: [],
      productTypeList: [],
      productShow: false,
      destinationUrlShow: false,
      uploadlimit: 1,
      upadlodData: {
        account_id: 10
      },
      uploadHeaders: {},
      uploadList: [
        {
          previewImgUrl: '',
          fileList: [
            {
              name: '',
              url: ''
            }
          ]
        }
      ],
      ADCREATIVE_STYLE_list: {},
      adcreativeList: [],
      activeAdcreativeList: false,
      Adcreativeinfo: {
        product_type: '',
        site_set: ''
      },
      adCampaignsList: [],
      site_setList: {},
      form: {
        campaign_id: '',
        product_refs_id: '',
        product_type: '',
        destination_url: '',
        site_set: '',
        adcreative_template_id: '',
        adcreative_elements: []
      },
      adCampaignsParams: {
        page: 1,
        page_size: 100,
        campaign_id: '',
        configured_status: '',
        campaign_name: '',
        start_date: '',
        end_date: ''
      },
      adcreativeTemplateInfo_row: {
        adcreative_sample_image: {}
      },
      tableDropdown: '下拉菜单',
      tabActiveName: '0',
      tabActiveList: [],
      tabList: [
        {
          label: '创意1',
          name: '0',
          adcreative_name: '',
          elementsData: {
            imageList: [], // 图片
            textList: [], // 文本
            videoList: [], // 视频
            urlList: [], // 链接
            enumList: [], // 选择器
            canvasList: [], // 画布
            referenceList: [] //TSA 外部资源引用类型，例如应用宝落地页 id 、视频说说 id 、微信小程序 id
          }
        },
        {
          label: '+ 添加',
          name: 'add',
          adcreative_name: '',
          elementsData: {
            imageList: [], // 图片
            textList: [], // 文本
            videoList: [], // 视频
            urlList: [], // 链接
            enumList: [], // 选择器
            canvasList: [], // 画布
            referenceList: [] //TSA 外部资源引用类型，例如应用宝落地页 id 、视频说说 id 、微信小程序 id
          }
        }
      ]
    }
  },
  methods: {
    // 创意规格过滤
    filterHandler(value, row) {
      return row['adcreative_template_style'] === value
    },
    remoteCampaign(query) {
      // if (query !== '') {
      this.loadingCampaign = true
      setTimeout(() => {
        this.adCampaignsParams.campaign_name = query
        this.getAdCampaigns(this.adCampaignsParams)
      }, 300)
    },
    newCampaign() {
      this.$router.push('/advertising/Campaign/new')
    },
    // 点击触发
    uploadFileLiclick(index, type) {
      this.tabActiveList = {
        index: index,
        type: type
      }
    },
    /**
     *  单图片和视频上传
     *  @param e
     *  @param tabIndex 创意下标
     *  @param index 元素下标 （结构体：结构体列表下标 ）
     *  @param type 元素类型
     *  @param elementNmae 元素名 （结构体）
     *  @param childrenIndex 子集下标 （结构体）
     */
    uploadfileChange(e, index, tabIndex, type, elementNmae, childrenIndex) {
      // console.log(index, tabIndex)
      var that = this // vm
      var target = e.target || e.currentTarget
      var file = target.files[0],
        tabTarget = {}
      var height, width, file_size, file_format
      // 判断是否为结构体
      if (elementNmae) {
        tabTarget =
          that.tabList[tabIndex]['elementsData'].structList[index].children[
            childrenIndex
          ][elementNmae]
      } else {
        //非结构体
        if (type == 'image') {
          tabTarget = that.tabList[tabIndex]['elementsData'].imageList[index]
        } else {
          tabTarget = that.tabList[tabIndex]['elementsData'].videoList[index]
        }
      }

      if (type == 'image') {
        var restriction = tabTarget.restriction
        ;(height = restriction.image_restriction.height),
          (width = restriction.image_restriction.width),
          (file_size = restriction.image_restriction.file_size),
          (file_format = restriction.image_restriction.file_format)
      } else if (type == 'video') {
        var restriction = tabTarget.restriction
        ;(height =
          restriction.video_restriction.min_height ||
          restriction.video_restriction.height),
          (width =
            restriction.video_restriction.min_width ||
            restriction.video_restriction.width),
          (file_size = restriction.video_restriction.file_size),
          (file_format = restriction.video_restriction.file_format)
      }
      if (!file) {
        // console.log(type + ':' + file)
        return false
      }

      // 格式校验
      if (file.size / 1024 > file_size) {
        this.$message('上传文件不能超过' + file_size + 'kb')
        return false
      }
      if (!this.formatCheck(file_format, file.type, type)) {
        this.$message('上传文件格式不正确')
        return false
      }
      //  根据类型 判断 宽高限制
      if (type == 'image') {
        //读取图片数据
        var reader = new FileReader()
        reader.onload = function(e) {
          var data = e.target.result
          //加载图片获取图片真实宽度和高度
          var image = new Image()
          image.onload = function() {
            var w = image.width
            var h = image.height
            if (width == w && height == h) {
              that.uploadFileAction(
                file,
                index,
                tabIndex,
                type,
                elementNmae,
                childrenIndex
              )
            } else {
              that.$message('上传文件尺寸不正确')
            }
          }
          image.src = data
        }
        reader.readAsDataURL(file)
      } else if (type == 'video') {
        var reader = new FileReader()
        var fileType = file.type
        reader.onload = function(e) {
          var data = e.target.result
          var video = document.createElement('video')
          document.body.appendChild(video)
          video.addEventListener('loadedmetadata', function(e) {
            if (video.videoWidth == width && video.videoHeight == height) {
              that.uploadFileAction(
                file,
                index,
                tabIndex,
                type,
                elementNmae,
                childrenIndex
              )
            } else {
              that.$message('上传文件尺寸不正确')
            }
            video.remove()
          })
          video.setAttribute('src', data)
        }
        reader.readAsDataURL(file)
      }
    },

    /**
     *
     *  @param file
     *  @param tabIndex 创意下标
     *  @param index 元素下标 （结构体：结构体列表下标 ）
     *  @param type 元素类型
     *  @param elementNmae 元素名 （结构体）
     *  @param childrenIndex 子集下标 （结构体）
     */
    uploadFileAction(file, index, tabIndex, type, elementNmae, childrenIndex) {
      var that = this,
        targetList,
        url = ''
      // 判断是否为结构体
      if (elementNmae) {
        targetList =
          that.tabList[tabIndex]['elementsData'].structList[index].children[
            childrenIndex
          ][elementNmae]
      } else {
        //非结构体
        if (type == 'image') {
          targetList = that.tabList[tabIndex]['elementsData'].imageList[index]
        } else {
          targetList = that.tabList[tabIndex]['elementsData'].videoList[index]
        }
      }

      if ('image' == type) {
        url = '/Admin/Marketing_Images/add'
      } else if ('video' == type) {
        url = '/Admin/Marketing_Videos/add'
      }
      var fd = new FormData()
      fd.append('account_id', that.account_id)
      fd.append('file', file)
      if ('video' == type) {
        fd.append('description', file)
      }
      var xhr = new XMLHttpRequest()
      xhr.upload.addEventListener(
        'progress',
        function(evt) {
          //console.log('progressprogressprogressprogressprogress')
          if (evt.lengthComputable) {
            targetList.progressbar.percentage = 60
            Math.round(evt.loaded * 100 / evt.total)
            targetList.progressbar.status = 'success'
          } else {
            if (complete) {
              targetList.progressbar.status = 'success'
            }
          }
        },
        false
      )
      xhr.addEventListener(
        'load',
        function(evt) {
          //console.log('服务器响应：', evt)
        },
        false
      )
      xhr.addEventListener(
        'error',
        function(evt) {
          targetList.progressbar.status = 'exception'
          console.log(evt)
        },
        false
      )
      xhr.addEventListener(
        'abort',
        function() {
          console.log(evt)
        },
        false
      )
      xhr.open('POST', url) //修改成自己的接口
      xhr.setRequestHeader('VUE-SESSID', this.uploadHeaders['VUE-SESSID'])
      xhr.send(fd)
      xhr.onreadystatechange = function() {
        if (xhr.readyState == 4) {
          if (xhr.status == 200) {
            var data = JSON.parse(xhr.response)
            if (data.code == 0) {
              // console.log('onreadystatechange:', data)
              targetList.progressbar.percentage = 100
              targetList.progressbar.status = 'success'
              // console.log(imageList)
              targetList.preview_url = data.data.preview_url
              if ('video' == type) {
                targetList.itemvalue = data.data.video_id
              } else {
                targetList.itemvalue = data.data.image_id
              }
            } else {
              that.$message(data.msg)
              targetList.progressbar.status = 'exception'
            }
          } else {
            targetList.progressbar.status = 'exception'
          }
        }
      }
    },

