    // 获取创意规格 -- 请求
    getAdcreativetemplateinfo() {
      var that = this
      this.loading = true
      var params = that.Adcreativeinfo
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Adcreativetemplateinfo/get', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            that.adcreativeList = respData.data.list
          } else {
            that.$message(respData.msg)
          }
          that.loading = false
        })
        .catch(function(err) {
          that.loading = false
          that.$message('网络繁忙，请稍后再试！')
          console.log(err)
        })
    },

    onCancel() {
      this.$router.push('/advertising/adCreative')
    },
    onSubmit() {
      var params = this.form
      var that = this,
        itemList = this.tabList // 创意列表
      if (!params.campaign_id) {
        that.$message('请选择推广计划')
        return false
      }
      if (!params.product_type) {
        that.$message('请选择推广计划')
        return false
      }

      if (!params.site_set) {
        that.$message('请选择投放站点')
        return false
      }
      if (!this.adcreativeTemplateInfo_row.adcreative_elements) {
        that.$message('请选择创意规格')
        return false
      }

      var filtration = false,
        filtrationText = '',
        adcreative_elements = [] // 创意列表

      // 筛选校验 数据
      // 遍历创意
      for (var index = 0; index < itemList.length - 1; index++) {
        var elementsData = itemList[index].elementsData //  单个创意元素数据
        var elementsList = new Object() //  创意
        elementsList['adcreative_name'] = itemList[index]['adcreative_name'] // 创意名
        // 判断创意名称
        if (
          !itemList[index]['adcreative_name'] &&
          itemList[index]['adcreative_name'].length > 120
        ) {
          filtration = true
          filtrationText =
            itemList[index]['name'] + '请填写字数在(1-120)的创意名称'
          break
        }
        // console.log('elementsData:', elementsData)
        // 遍历拼装一个创意的 类型 列表
        var typeDataList = [] //
        for (var key in elementsData) {
          if (key == 'structList') {
            for (var k in elementsData[key]) {
              var objList = elementsData[key][k]['children']
              for (var i in objList) {
                for (var name in objList[i]) {
                  typeDataList.push(objList[i][name])
                }
              }
            }
          } else {
            for (var k in elementsData[key]) {
              if (
                'ELEMENT_TYPE_STRUCT' != elementsData[key][k]['element_type']
              ) {
                typeDataList.push(elementsData[key][k])
              }
            }
          }
        }
        // console.log('typeDataList:', typeDataList)
        //  遍历创意的类型列表 --  格式校验
        for (var key in typeDataList) {
          var itemTarget = typeDataList[key]
          var n = index + 1
          // 判断类型
          if (itemTarget.element_type == 'ELEMENT_TYPE_IMAGE') {
            // 图片
            // 判断是否 必填项
            if (itemTarget.required && !itemTarget.itemvalue) {
              filtration = true
              filtrationText = '创意' + n + '请上传图片'
              break
            }
          } else if (itemTarget.element_type == 'ELEMENT_TYPE_VIDEO') {
            // 视频
            // 判断是否 必填项
            if (itemTarget.required && !itemTarget.itemvalue) {
              filtration = true
              filtrationText = '创意' + n + '请上传视频'
              break
            }
          } else if (itemTarget.element_type == 'ELEMENT_TYPE_TEXT') {
            // 文本类型
            // 创意
            if (itemTarget.required && !itemTarget.itemvalue) {
              filtration = true
              filtrationText = '创意' + n + '请填写' + itemTarget.description
              break
            } else if (itemTarget.itemvalue) {
              if (
                itemTarget.restriction &&
                itemTarget.restriction.text_restriction
              ) {
                if (
                  itemTarget.restriction.text_restriction.max_length <
                    itemTarget.itemvalue.length ||
                  itemTarget.restriction.text_restriction.min_length >
                    itemTarget.itemvalue.length
                ) {
                  filtration = true
                  filtrationText =
                    '请输入' +
                    itemTarget.restriction.text_restriction.min_length +
                    '~' +
                    itemTarget.restriction.text_restriction.max_length +
                    '的字符'
                  break
                }
              }
            }
          } else if (itemTarget.element_type == 'ELEMENT_TYPE_URL') {
            // URL
            if (itemTarget.required && !itemTarget.itemvalue) {
              filtration = true
              filtrationText = '创意' + n + itemTarget.description + '不能为空'
              break
            } else if (itemTarget.itemvalue) {
              if (
                itemTarget.restriction &&
                itemTarget.restriction.url_restriction
              ) {
                var url_pattern =
                  itemTarget.restriction.url_restriction.url_pattern
                var reg = new RegExp(url_pattern)
                if (!reg.test(itemTarget.itemvalue)) {
                  filtration = true
                  filtrationText =
                    '创意' + n + itemTarget.description + '格式不正确'
                  break
                }
              }
            }
          } else if (itemTarget.element_type == 'ELEMENT_TYPE_ENUM') {
            // 枚举类型
            if (itemTarget.required && !itemTarget.itemvalue) {
              filtration = true
              filtrationText = '创意' + n + '请选择' + itemTarget.description
              break
            }
          }
        }
        //  格式校验是否通过
        if (filtration) {
          break
        }

        //  创意上传格式 拼装
        for (var key in elementsData) {
          var list = elementsData[key] //
          for (var i in list) {
            //console.log('ii:', list[i])
            if (list[i]['children']) {
              var childrenList = list[i]['children']
              var children = []

              for (var c in childrenList) {
                var obj = {}
                for (var name in childrenList[c]) {
                  obj[name] = childrenList[c][name]['itemvalue']
                }
                children.push(obj)
              }
              if (children.length == 1) {
                elementsList[list[i]['parent']['name']] = children[0]
              } else {
                elementsList[list[i]['parent']['name']] = children
              }
            } else {
              elementsList[list[i]['name']] = list[i]['itemvalue']
            }
          }
        }

        // console.log('单个创意 elementsList :', elementsList)
        //  return false
        // 添加多个创意
        adcreative_elements.push(elementsList)
      }
      //  必填项 校验
      if (filtration) {
        that.$message(filtrationText)
        return false
      }
      // 添加创意列表
      params.adcreative_elements = adcreative_elements
      that.submitLoading = true
      //  添加创意请求
      params.account_id = this.account_id

      this.$axios
        .get('/Admin/Marketing_Adcreatives/add', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          that.submitLoading = false
          if (respData.code == 0) {
            that.$message({
              type: 'success',
              message: respData.msg
            })
            setTimeout(function() {
              that.$router.push('/advertising/adCreative')
            }, 500)
          } else {
            that.$message.error(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          that.submitLoading = false
          console.log(err)
        })
    },

    getAdCommongetEnum() {
      var that = this
      this.$axios
        .get('/Admin/Marketing_Common/getEnum', {})
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            //  console.log('data:', respData.data)
            that.element_type_list = respData.data['ELEMENT_TYPE']
            that.site_setList = respData.data['SITE_SET']
            that.ADCREATIVE_STYLE_list = respData.data['ADCREATIVE_STYLE']
            that.productTypeList = respData.data['PRODUCT_TYPE']
            // console.log(that.site_setList)
            // console.log(that.ADCREATIVE_STYLE_list)
          }
        })
        .catch(function(err) {
          console.log(err)
        })
    },
    getProducts: function(params) {
      var that = this
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Products/list', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            that.productList = respData.data
          } else {
            that.$message(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
        })
    },
    getAdCampaigns: function(params) {
      var that = this
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Campaigns/get', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            if (respData.data.list || respData.data.page_info) {
              that.adCampaignsList = respData.data.list
              that.loadingCampaign = false
              console.log(that.adCampaignsList)
              //that.page_info = respData.data.page_info
            }
          } else {
            that.$message(respData.msg)
            that.loadingCampaign = false
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          that.loadingCampaign = false
        })
    }
  }
}
</script>


<style scoped>
.temp-radio-item {
  width: 100%;
  margin: 0 0 20px 0;
}
.temp-radio-remark {
  display: inline-block;
  color: #999;
  font-size: 14px;
  margin-left: 100px;
}

.radio-inlineblock {
  display: inline-block;
  margin-left: 20px;
}

.sample_image_info {
  padding: 20px 0 0 0;
  background: #f8f9fa;
  text-align: center;
}

.sample_image_info p {
  color: #555;
  font-size: 14px;
  text-align: center;
}

.sample_image_info p span {
  color: #999;
  font-size: 12px;
}

.sample_image_info span {
  color: #555;
  font-size: 13px;
  text-align: center;
}

.upload-size {
  font-size: 16px;
  color: #ff5722;
}

.upload-size span {
  font-size: 12px;
  color: #999;
}

.uploadList-wrap {
  width: 100%;
  margin-bottom: 20px;
  float: left;
  position: relative;
  z-index: 2;
}

.upload-li {
  display: inline-block;
}

.uploadList-li {
  float: left;
  width: 360px;
  display: inline-block;
  border: 1px dashed #d9d9d9;
  background: #fff;
  box-sizing: border-box;
  border-radius: 6px;
  text-align: center;
  cursor: pointer;
  position: relative;
  z-index: 2;
  padding-bottom: 10px;
  margin-right: 10px;
  margin-bottom: 10px;
}

.upload-demo {
  width: 360px;
  display: inline-block;
  margin-right: 20px;
}

.uploadurl-input {
  width: 600px;
  margin-bottom: 15px;
}

.uploadfile-item {
  position: relative;
  z-index: 3;
}

.uploadfile-item .icon-upload {
  font-size: 57px;
  color: #c0c4cc;
}

.uploadfile-input {
  position: absolute;
  z-index: 5;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}

.uploadfile-preview {
  width: 100%;
}
</style>



